import React, { Component } from 'react'
import axios from 'axios'
const URI = 'https://sdbackend.onrender.com/api/users/';


export default class CreateUser extends Component {
    state = {
        users: [],
        username: '',
        edting: false,
        _id: null
    }
    async componentDidMount() {
        this.getUsers();
        console.log(this.state.users);
    }
    getUsers = async () => {
        const res = await axios.get(URI);
        this.setState({ users: res.data });
    }
    onSubmit = async e => {
        e.preventDefault();
        if(this.state.edting){
         await axios.put(URI+this.state._id,{username: this.state.username})
        }else{
        await axios.post(URI, {
            username: this.state.username
        })
        }
        this.setState({ username: '' });
        this.setState({edting: false});
        this.getUsers();
    }

    deleteUser = async (id) => {
        await axios.delete(URI + id);
        this.getUsers();
    }
    getUser = async (id) =>{
        const res = await axios.get(URI+id);
        this.setState({username: res.data.username});
        this.setState({edting: true});
        this.setState({_id: id});
    }
    editUser = async (id) => {
        this.getUser(id);
        await axios.put(URI+id);
        this.getUsers();
    }
    onChangeUsername = (e) => {
        this.setState({
            username: e.target.value
        })
    }

    render() {
        return (
            <div className="row">
                <div className="col-md-4">
                    <div className="card card-body">
                        <h3>Create New User</h3>
                        <form onSubmit={this.onSubmit}>
                            <div className="form-group">
                                <input
                                    type="text"
                                    className="form-control"
                                    value={this.state.username}
                                    onChange={this.onChangeUsername}
                                />
                            </div>
                            <button type="submit" className="btn btn-primary">
                                Save
                            </button>
                        </form>
                    </div>
                </div>
                <div className="col-md-8">
                    <ul className="list-group">
                        {
                            this.state.users.map(user => (

                                <li
                                    className="list-group-item list-group-item-action"
                                    key={user._id}
                                    onDoubleClick={() => this.deleteUser(user._id)}
                                    onClick={()=>this.getUser(user._id)}
                                >
                                    {user.username}
                                </li>
                            )

                            )
                        }
                    </ul>
                </div>

            </div>

        )
    }
}
